* {  
  transition: all 1s;
}
body {
  margin:5% 5% 5% 5%;
  font-family:Arial, Helvetica, sans-serif;
}
#root {
  margin:0;
  padding:0;
}
h1 {
  color:slategrey;
  text-align: center;
  margin-top:-2.6em;
  margin-bottom:1em;
  font-size:1.5em;
}
h1.new-game {
  color:white;
}
.game {
  width:calc(28%);
  margin:0 auto;
  border: 1px solid lightslategrey;
  padding:2% 2% 2% 2%;
  background-color:lightgrey;
  border-radius:25px;
}
@media (max-width: 1000px) {
  .game {
    width:calc(35%);
  }
}
@media (max-width: 768px) {
  h1 {
    margin-top:-2em;
  }
  .game {
    margin-top:45px;
    width:calc(90%);
    padding:2%;
  }
}
button {
  width:auto;
  border:1px solid lightslategray;
  margin:0 auto;
  display:block;
  background-color:lightslategrey;
  color:white;
  padding:10px;
  border-radius: 5px;
  transition:all 2s;
  text-transform: uppercase;
  font-size:.6em;
  transition:all .5s;
  cursor:pointer
}
@media (hover:hover) {
  button:hover {
    background-color:white;
    border:1px solid lightslategrey;
    color:darkslategrey;
  }
}
button:active {
  color:lightslategrey;
  background-color:white;
}
.button-container {
  display:flex;
  justify-content:center;
}
 
.purple {
  background-color:purple;
}
.green {
  background-color:green;
}
.blue {
  background-color:blue;
}
.orange {
  background-color:orange;
}
.yellow {
  background-color:yellow;
}
.salmon {
  background-color:salmon;
}
.none {
  background-color:lightgrey;
}
.aqua {
  background-color:aqua;
}
.maroon {
  background-color:maroon;
}
 
form {
  text-align:center;
  width:auto;
  margin:0 auto;
  display:block;
}
select {
  width:50px;
  height:50px;
  border-radius:50%;
  margin-right:10px;
  font-size:0em;
}
option {
  font-size:1em;
}
 
h2 {
  margin:0px 0;
  padding:0;
  font-size:1.2em;
  text-align: center;
  color:slategray
}
.row {
  height:auto;
  margin-top:10px;
  clear:both;
  position: relative;
  border-bottom:1px solid lightslategrey;
}
ul.guesses {
  width:80%;
  padding:0;
  margin:0 auto;
  text-align: center;
 
}
ul.guesses li {
  display:inline-block;
  list-style-type:none;
  width:40px;
  height:40px;
  border:1px solid lightslategray;
  border-radius:50%;
  margin-right:2%;
  font-size:.7em;
  font-size:0em;
}
@media (max-width: 800px) {
  ul.guesses {
  }
  ul.guesses li {
    width:30px;
    height:30px;
  }
}
 
ul.answer {
  margin:0px 0;
  width:100%;
  padding:0;
  text-align: center;
}
ul.answer li {
  display:inline-block;
  width:20px;
  height:20px;
  border-radius:50%;
  border:1px solid grey;
  font-size:.5em;
  font-size:0em;
  margin-bottom:10px;
  margin-right:1%;
}
 
@media (max-width: 800px) { 
  ul.answer {
  }
  ul.answer li {
    width:10px;
    height:10px;
  }
}
.red {
  background-color:red;
}
.white {
  background-color:white;
}
ul.answer li.none {
  background-color:grey;
}
form {
  clear:both;
}
 
.modal {
  position:fixed;
  width:90%;
  height:90%;
  display:block;
  background-color:white;
  /*border:1px solid slategrey;*/
  border-radius:15px;
  z-index:1;
  transition: all .5s;
  overflow:hidden;
  color:slategrey
}
.modal form {
  padding:20px;
}
.modal form input {
  padding: 10px;
  border-radius:6px;
  margin-top:5px;
  margin-bottom:20px;
  width:20px;
  border:1px solid lightslategrey;
  text-align: center;
  transition: all 2s;
}
 
.hidden {
  /*left:-100%;*/
  height:0%;
  opacity:0;
  transition: all .3s;
}